html {
	box-sizing: border-box;
}

*,
*:before,
*:after {
	box-sizing: inherit;
}

body {
	background-color: #E5E5E5
}

.home-container {
	display: grid;
	grid-template-rows: 56px 470px 150px;
}

.home-logo {
	display: grid;
	width: 260px;
	height: 56px;
	margin-left: 2%;
	margin-top: 2%;
}

.home-body {
	display: grid;
	font-family: "Times New Roman", Times, serif;
	font-style: italic;
	line-height: normal;
	font-size: 4.8em;	
	justify-content: center;
	align-items: center;
}

.home-apply {
	display: grid;	
	justify-content: center;
	align-items: center;
	margin-left: auto;
  	margin-right: auto;
}

.home-vector1 {
	width: 235.88px;
	height: 133.78px;
	opacity: 0.95;
	margin-top: 65px;
	margin-left: 10px;
}
.home-apply-link {
	position: fixed;
	color: white;
	text-decoration: none;
	font-family: "Arial", sans-serif;
	font-weight: bolder;
	font-size: 24px;
	margin-top: 108px;
	margin-left: 65px;
}