html {
	box-sizing: border-box;
}

*,
*:before,
*:after {
	box-sizing: inherit;
}

body {
	background-color: #E5E5E5
}

.home-container {
	display: grid;
	grid-template-rows: 56px 470px 150px;
}

.home-logo {
	display: grid;
	width: 260px;
	height: 56px;
	margin-left: 2%;
	margin-top: 2%;
}

.home-body {
	display: grid;
	font-family: "Times New Roman", Times, serif;
	font-style: italic;
	line-height: normal;
	font-size: 4.8em;	
	justify-content: center;
	align-items: center;
}

.home-apply {
	display: grid;	
	justify-content: center;
	align-items: center;
	margin-left: auto;
  	margin-right: auto;
}

.home-vector1 {
	width: 235.88px;
	height: 133.78px;
	opacity: 0.95;
	margin-top: 65px;
	margin-left: 10px;
}
.home-apply-link {
	position: fixed;
	color: white;
	text-decoration: none;
	font-family: "Arial", sans-serif;
	font-weight: bolder;
	font-size: 24px;
	margin-top: 108px;
	margin-left: 65px;
}
html {
	box-sizing: border-box;
}

*,
*:before,
*:after {
	box-sizing: inherit;
}

body {
	background-color: #E5E5E5
}

.portal-container {
	display: grid;
}

form {
	display: grid;
	grid-template-columns: 325px 325px;
	grid-column-gap: 100px;	
	grid-row-gap: 20px;
	justify-content: center;
	align-items: center;
	margin-top: 8%
}

.portal-logo {
	width: 260px;
	height: 56px;
	margin-left: 2%;
	margin-top: 2%;
}

.upload {
	grid-column: 2;
	grid-row: 5 / 7;
}

.submit {
	background-color: #FC08D5; /* Pink */
	border: none;
	color: white;
	text-align: center;
	text-decoration: none;
	display: inline-block;
	font-size: 16px;
	height: 32px;
	width: 80px;
}

.disclaimer {
	display: grid;
	font-family: "Times New Roman", Times, serif;
	font-style: italic;
	font-size: .6em;
	grid-column: 1 / 3;
}
